import fetch from "isomorphic-unfetch";

import MasterPage from "../componentes/Master";
import Precio from "../componentes/Precio";
import Noticias from "../componentes/Noticias";
import Eventos from "../componentes/Eventos";

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del BitCoin</h2>
                <Precio
                    precio={props.precioBitcoin}
                />
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre BitCoin</h2>
                <Noticias
                    noticias={props.noticiasBitcoin}
                />
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos BitCoin</h2>
                <Eventos
                    eventos={props.eventosBitcoin}
                />
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
    const precio = await fetch('https://min-api.cryptocompare.com/data/pricemultifull?fsyms=BTC&tsyms=USD');
    const jsonPrecio = await precio.json();
    const resPrecio = await jsonPrecio.DISPLAY['BTC']['USD'];

    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&language=es&apiKey=1926f2bb78fb439896e11f22e100cd7c');
    const jsonNoticias = await noticias.json();
    const resNoticias = await jsonNoticias.articles;

    const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=RHG53OLQY3DSVNYHIPMZ&locale=es_ES');
    const jsonEventos = await eventos.json();
    const resEventos = await jsonEventos.events;

    return {
        precioBitcoin: resPrecio,
        noticiasBitcoin: resNoticias,
        eventosBitcoin: resEventos
    }
}

export default Index;
